 vertical-align: baseline;\n}\n\nh1,\nh2,\nh3,\nh4 {\n  margin: 0;\n}\n\nul,\nol {\n  margin: 0;\n  padding: 0;\n}\n\nli {\n  list-style: none;\n}\n\ntable {\n  border-collapse: collapse;\n}\n\ntable caption {\n  display: none;\n}\n\nfigure {\n  margin: 0;\n}\n\nimg {\n  max-width: 100%;\n  object-fit: contain;\n}\n\na {\n  color: inherit;\n  text-decoration: none;\n  cursor: pointer;\n}\n\np {\n  margin: 0;\n}\n\ni, em, address {\n  font-style: normal;\n}\n\nstrong {\n  font-weight: inherit;\n}\n\nem {\n  font-weight: inherit;\n}\n\nfieldset {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  min-width: 0;\n}\n\nlegend {\n  background-color: transparent;\n  border: 0;\n  color: transparent;\n  font-size: 0;\n  text-shadow: none;\n}\n\nbutton {\n  padding: 0;\n  border: 0;\n  background: none;\n  outline: 0;\n  user-select: none;\n}\n\nlabel {\n  border: 0;\n  cursor: pointer;\n}\n\ninput {\n  -webkit-appearance: none;\n  appearance: none;\n  outline: none;\n  border: 0;\n  border-radius: 0;\n  box-sizing: border-box;\n  vertical-align: middle;\n}\n\ninput::-ms-clear {\n  display: none;\n}\n\ninput::-webkit-search-cancel-button {\n  display: none;\n}\n\ninput[type=\"file\"]::-webkit-file-upload-button {\n  opacity: 0;\n}\n\ninput[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n\nhtml body.fb input:focus + span {\n  outline: 1px dotted #999 !important;\n}\n\nselect {\n  outline: none;\n  appearance: none;\n  box-sizing: border-box;\n  border-radius: 0;\n}\n\nselect::-ms-expand {\n  display: none;\n}\n\nselect:disabled {\n  opacity: 1;\n}\n\ntextarea {\n  resize: none;\n  -webkit-border-radius: 0;\n  -webkit-appearance: none;\n  border-radius: 8px;\n  background: #fff;\n  color: #111;\n  text-indent: 0;\n}\n\ntextarea::placeholder {\n  color: #999;\n}\n\ndetails summary {\n  display: block;\n  list-style: none;\n}\n\ndetails summary::-webkit-details-marker {\n  display: none;\n}\n\n/* 페이지 */\n/**\r\n* Created by jmju on 2023.01.25 [공통 SCSS]\r\n*/\n.mj__title--hidden {\n  overflow: hidden;\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  clip: rect(1px, 1px, 1px, 1px);\n}\n\n.mj input::placeholder {\n  color: #999;\n  opacity: 1;\n}\n\n.mj input:-ms-input-placeholder {\n  color: #999;\n}\n\n.mj input::-ms-input-placeholder {\n  color: #999;\n}\n\n.mj input::-ms-clear {\n  display: none;\n}\n\n.mj input:focus {\n  color: #111;\n  border: 1px solid #111;\n}\n\n.mj input:disabled, .mj input:read-only:not([type=submit]) {\n  background-color: #f7f8f9 !important;\n  color: #999 !important;\n}\n\n.mj input[type=\"text\"], .mj input[type=\"tel\"] {\n  height: 40px;\n  padding: 0 10px;\n  background: #fff;\n  border: 1px solid #ddd;\n  color: #111;\n  font-size: 14px;\n  vertical-align: middle;\n  box-sizing: border-box;\n}\n\n.mj input[type=\"password\"] {\n  height: 40px;\n  padding: 0 10px;\n  border: 1px solid #ddd;\n  background: #fff;\n  color: #111;\n  font-size: 14px;\n  vertical-align: middle;\n  box-sizing: border-box;\n}\n\n.mj input[type=\"number\"] {\n  height: 40px;\n  padding: 0 10px;\n  border: 1px solid #ddd;\n  background: #fff;\n  color: #111;\n  font-size: 14px;\n  vertical-align: middle;\n  box-sizing: border-box;\n  -moz-appearance: textfield;\n}\n\n.mj input[type=\"number\"]::-webkit-outer-spin-button, .mj input[type=\"number\"]::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n\n.mj input[type=\"checkbox\"] {\n  width: 20px;\n  height: 20px;\n  margin: 0;\n  cursor: pointer;\n}\n\n.mj input[type=\"checkbox\"]:checked {\n  background-position: -100px 0px;\n}\n\n.mj input[type=\"checkbox\"]:disabled {\n  background-position: -300px 0px;\n}\n\n.mj input[type=\"checkbox\"]:disabled:checked {\n  background-position: -200px 0px;\n}\n\n.mj input[type=\"checkbox\"]:disabled + label {\n  color: #999;\n}\n\n.mj input[type=\"checkbox\"] + label {\n  font-size: 14px;\n  color: #666;\n  margin-left: 8px;\n}